header {
    width: 100% !important;
    box-shadow: 1rem 1rem 1rem 1rem rgba(0, 0, 0, .05);
    border-bottom: 1px solid darkgray;
    white-space: nowrap;
    min-height: 9vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 1;
}

footer {
    width: 100%;
    box-shadow: 1rem 1rem 1rem 1rem rgba(0, 0, 0, .05);
    border-top: 1px solid darkgray;
    min-height: 12vh;
    white-space: nowrap;
    text-align: center;
}

main {
    display: block;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: auto;
}

.main-section {
    /* height: 1500px; */
    width: 98%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.sections-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    background-color: whitesmoke;
    min-height: 81vh;
}

.left-section {
    width: 1%;
}

.right-section {
    width: 1%;
}

.return-button {
    margin: 10%;
    cursor: pointer;
    /* width: auto;
    height: auto; */
}

.change-language-button {
    cursor: pointer;
}

.header-left-section {
    display: flex;
}

.header-right-section {
    display: flex;
    justify-content: flex-end;
}

.options-button {
    cursor: pointer;
    scale: 1.4;
    margin: 10px;
    border: 1px solid black;
    border-radius: 10px;
    box-shadow: 1px 1px 2px 0px rgba(66, 68, 90, 1);
}

.return-button {
    position: absolute;
    top: 80px;
    left: 3%;
    margin: 0;
}

.common-button{
    cursor: pointer;
}